
import { axios } from "&"

export function changeMobile(payload) {

    return {
        type: "changeMobile",
        payload: payload
    }

}
export function clearmobile(payload) {
    return {
        type: "clearmobile",
        payload
    }
}
export function changeflag(payload) {
    return {
        type: "changeflag",
        payload
    }
}

export function changeCode(payload) {
    const cReg = /^\d{4}$/

    if (cReg.test(payload.code)) {


        return {
            type: "changeCode",
            payload
        }
    } else {
        return {
            type: "changeCode",
            payload
        }
    }
}

export async function getMobile({ url }) {
    const res = await axios.post(url);
    return {
        type: "changeUser",
        payload: {
            mobile: res.data.result
        }
    }
}

export const changeUser = (payload) => {
    return {
        type: "changeUser",
        payload
    }
}
