import "./index.scss"
import React, { Component } from "react"
import { Icon, InputItem } from "antd-mobile"
import bgImg from "@/assets/images/login.gif"
import { connect } from "react-redux"
import store from "../../react-redux/store";
import { axios } from "&";


import { changeMobile, clearmobile, changeCode } from "../../react-redux/actions"

@connect(
    state => {
        return {
            mobile: state.getIn(["mobile", "mobile"]),
            flag: state.getIn(["mobile", "flag"]),
            code: state.getIn(["mobile", "code"])

        }
    },

)



class Login extends Component {
    componentWillMount() {
     
    }
    back = () => {
        this.log.style.display = "block"
        this.code.style.display = "node"
        this.icon.style.display = "none"

    }

    getCode = () => {
        // 发送验证码


        const Reg = /^1(3|4|5|6|7|8|9)\d{9}$/;
        if (Reg.test(this.mobile.value)) {
            this.logbtn.style.backgroundColor = "skyblue"
            this.logbtn.style.disabled = false
            this.log.style.display = "none"
            this.code1.style.display = "block"
            this.icon.style.display = "block"
            axios.post("/react/aly/sendSms", {
                mobile: this.mobile.value
            }).then(res => {
                console.log(res)
                sessionStorage.code = res.data.param
            })


            axios.post("/react/checkCode", {
                mobile: this.mobile.value,
                code: sessionStorage.code
            }).then(res => {
                console.log(res)

            })


        } else {
            this.logbtn.style.backgroundColor = " rgba(255, 255, 255, 0.5)"
            this.logbtn.disabled = true
            // this.code1.style.display = "none"


        }

    }
    changeCode = (code) => {

        const Reg = /^\d{4}$/

        // 得到验证码并对比
        if (Reg.test(this.code.value)) {
            console.log(this.code.value)
            axios.post("/react/checkCode", {
                mobile: this.mobile.value,
                code: this.code.value
            }).then(res => {
                if (!!res.data.type) {
                    sessionStorage.token = res.data.token;
                    sessionStorage.mobile = this.mobile.value;
                    this.props.history.push("/main/mine");




                } else {
                    sessionStorage.token = "";
                    sessionStorage.mobile = "";
                }
            })

        }


    }
    render() {
        const {
            mobile,
            flag,
            code,
        } = this.props
        return (
            <div className="login">
                <img className="bgImg" src={bgImg} alt="" />

                <div className="top">
                    <div className="fan">  <i ref={el => this.icon = el}><Icon type="" /></i></div>
                    <div className="fan">  <i className="icon" onClick={() => this.back()} ref={el => this.icon = el}><Icon type="left" /></i></div>


                    <h1 className="tit">登录解锁精彩世界</h1>
                    <div className="deng" ref={el => this.log = el} >
                        <span>未注册手机验证后即将完成注册</span>
                        <div className="log"><input className="loginp" value={mobile} onChange={() => this.props.dispatch(changeMobile(this.mobile.value))} ref={el => this.mobile = el} type="tel" placeholder="请输入手机号" />
                            <p style={{ display: mobile ? "block" : "none" }} onClick={() => this.props.dispatch(clearmobile(""))} className="clear">×</p></div>
                        <button disabled={flag} className="logbtn" ref={el => this.logbtn = el} onClick={() => this.getCode()} >获取验证码</button>


                        <p style={{ marginTop: "3.6rem", fontSize: 14 }}>其他方式登录</p>
                        <div><i onClick={this.goWeixin} className="iconfont iconfont1" >&#xe678;</i   ></div>
                        <i>登录注册代表同意<i>用户协议</i>及<i>隐私政策</i></i>
                    </div>

                    <div className="fan1" ref={el => this.code1 = el} >


                        <div style={{ marginTop: "3rem" }}></div>
                        <span>验证码已经发送{mobile}</span>
                        <p>    <input className="codeinp" value={code} onChange={() => this.changeCode(this.code.value)} ref={el => this.code = el} type="tel" placeholder="请输入验证码" /></p>
                    </div>

                </div>
            </div>
        )
    }

    componentDidUpdate() {
        const Reg = /^1(3|4|5|6|7|8|9)\d{9}$/;
        if (Reg.test(this.mobile.value)) {
            this.logbtn.style.backgroundColor = "skyblue"
            this.logbtn.disabled = false


        } else {
            this.logbtn.style.backgroundColor = " rgba(255, 255, 255, 0.5)"
            this.logbtn.disabled = true

        }



    }
}

export default Login

